import React from 'react'
import { HomeContainer, HomeH1, HomeP, } from './home.elements'
import Lottie from "react-lottie";
import drnko_otp_logo from './drnko_otp_logo.json'
import drnko_app_logo from './drnko_app_logo.json'
import drnko_otp_Verfied_logo from './app_otp_Verfied_logo.json'
import drnko_fulllogo from './app_fulllogo.json'
import MovingComponent from 'react-moving-text'

const HomeSection = () => {
    const app_otp_logo = {
        loop: true,
        autoplay: true,
        animationData: drnko_otp_logo,
        rendererSettings: {
            preserveAspectRatio: "xMidYMid slice"
        }
    }
const app_logo = {
        loop: true,
        autoplay: true,
        animationData: drnko_app_logo,
        rendererSettings: {
            preserveAspectRatio: "xMidYMid slice"
        }
    }
const app_otp_Verfied_logo = {
        loop: true,
        autoplay: true,
        animationData: drnko_otp_Verfied_logo,
        rendererSettings: {
            preserveAspectRatio: "xMidYMid slice"
        }
    }

const app_fulllogo = {
        loop: true,
        autoplay: true,
        animationData: drnko_fulllogo,
        rendererSettings: {
            preserveAspectRatio: "xMidYMid slice"
        }
    }

  return (
    <HomeContainer>
        
        <HomeH1>
                <Lottie options={app_fulllogo} height={100} width={400}></Lottie>
                <HomeP><MovingComponent type="fadeIn" duration="2000ms" delay="0s" direction="normal" timing="ease" iteration="1" fillMode="none">Drnko Full Logo</MovingComponent></HomeP>

                <Lottie options={app_logo} height={132} width={132}></Lottie>
                <HomeP><MovingComponent type="fadeIn" duration="2000ms" delay="0s" direction="normal" timing="ease" iteration="1" fillMode="none">Drnko App Logo</MovingComponent></HomeP>

                <Lottie options={app_otp_Verfied_logo} height={300} width={300}></Lottie>
                <HomeP><MovingComponent type="fadeIn" duration="2000ms" delay="0s" direction="normal" timing="ease" iteration="1" fillMode="none">Drnko OTP Verified Logo</MovingComponent></HomeP>

                <Lottie options={app_otp_logo} height={200} width={300}></Lottie>
                <HomeP><MovingComponent type="fadeIn" duration="2000ms" delay="0s" direction="normal" timing="ease" iteration="1" fillMode="none">Drnko OTP Notify Logo</MovingComponent></HomeP>
        </HomeH1>

    </HomeContainer>
  )
}

export default HomeSection
